                  "
                >
                  <p style="color: #dddddd">{{ parseDate(item.date) }}</p>
                  <p style="color: white">{{ item.title }}</p>
                </div></v-img
              >
            </v-card>
          </v-flex>
        </v-layout>
        <div class="text-center;">
          <v-pagination
