          >
            <v-card
              dark
              @click="
                selectedItemIndex = ind;
                overlay = !overlay;
              "
            >
              <v-img
                :src="
                  item.thumbnail == ''
                    ? require('../assets/img/default_thumbnail.jpeg')
                    : item.thumbnail
                "
                height="350px"
              ></v-img>
              <div style="padding: 10px">
                <p style="color: #dddddd">
                  {{ parseDate(item.date) }}
                </p>
                <p style="color: white">
                  {{ item.tweet }}
                </p>
              </div>
            </v-card>
          </v-flex>
        </v-layout>
        <div class="text-center;">
          <v-pagination
            v-model="currentPageUpdates"
            :length="numberOfPagesUpdates"
            :total-visible="7"
            circle
            dark
            style="color: white"
          ></v-pagination>
        </div>
      </v-tab-item>
    </v-tabs-items>
  </v-container>
